@import './_variables.scss';
* {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
}
a {
   text-decoration: none;
   color: #000;
}
html{
	font-size: 15px;
}
body,
.wrapper {
   width: 100%;
   height: 100%;
   font-family: 'Roboto';
}
.wrapper {
   min-height: 100vh;
   display: flex;
   flex-direction: column;
}
[class*='container'] {
   max-width: $container+px;
   margin: 0px auto;
   height: 100%;
	padding-left: 15px;
	padding-right: 15px;
}